import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Helmet } from 'react-helmet';

const Restaurant = () => {
    useEffect(() => {
        document.title = 'Pereztroika - Dner du Peuple 2025';
    }, []);

    const { t } = useTranslation();

    const [loading, setLoading] = useState(true);
    useEffect(() => {
        setTimeout(() => {
            setLoading(false);
        }, 3500);
    }, []); 

    return (
        
        <div className="restaurant-container">
            <Helmet>
                <title>Pereztroika - Dner du Peuple 2025</title>
                <meta name="description" content="Convocation officielle : Votre prsence est requise au Restaurant Pereztroika. Cuisine ukrainienne authentique dans une ambiance sovitique." />
                
                {/* Open Graph / Facebook */}
                <meta property="og:type" content="website" />
                <meta property="og:title" content="PEREZTROIKA - Dner du Peuple 2025" />
                <meta property="og:description" content=" CONVOCATION OFFICIELLE  Restaurant du Camarade - Cuisine Ukrainienne  Ambiance Sovitique. Samedi 8 novembre  19h30." />
                <meta property="og:image" content="https://../pereztroika.jpg" />
                <meta property="og:image:width" content="1200" />
                <meta property="og:image:height" content="630" />
                
            </Helmet>

            {/* Loading Animation */}
            {loading && (
                <div className="loading-overlay">
                    <div className="loading-content">
                        <div className="loading-symbol"></div>
                        <h1 className="loading-title">PEREZTROIKA</h1>
                        <div className="loading-bar-container">
                            <div className="loading-bar"></div>
                        </div>
                        {/* <div className='loading-spinning'></div> */}
                    </div>
                </div>
            )}

            {/* Main Content */}
            <div className="main-content">
                
                {/* Communist banner top */}
                <div className='header-container'>
                    <div className="communist-banner">
                        <div className="banner-symbol banner-symbol-left"></div>
                        <div className="banner-symbol banner-symbol-right"></div>
                        <p className="banner-text"> DNER DU PEUPLE 2025 </p>
                    </div>

                    {/* Header - Restaurant Name */}
                    <header className="restaurant-header">
                        <div className="restaurant-header-content">

                            <h1 className="restaurant-name">
                                PEREZTROIKA
                            </h1>
                            
                            <div className="restaurant-badge">
                                <p className="badge-text">RESTAURANT DU CAMARADE</p>
                            </div>

                            <p className="restaurant-subtitle">
                                Cuisine Ukrainienne  Ambiance Sovitique
                            </p>
                        </div>
                    </header>
                </div>

                {/* Propaganda poster style message */}

                {/* Restaurant Description */}
                <section className="description-section">
                    <div className="description-content">
                    <div className="propaganda-message">
                        <p className="propaganda-text"> CONVOCATION OFFICIELLE </p>
                    </div>
                        <p className="description-intro">
                            <span className="text-semibold"> Votre prsence est requise au </span> <span className="highlight-bold">Restaurant Pereztroika</span>, 
                            <span className="text-semibold"> tablissement d'tat o l'hospitalit ukrainienne rencontre la nostalgie sovitique</span>! 
                        </p>
                        
                        <p className="description-footer">
                            Ici, nous servons la <em className="highlight-emphasis">vraie</em> cuisine du peuple : 
                            authentique, copieuse, et accompagne de chants rvolutionnaires en fond sonore.
                        </p>
                    </div>
                </section>

                {/* Event Details - Propaganda style */}
                <section className="event-section">
                    <div className="event-stars-top">  </div>

                    <p className="event-order">ORDRE DU PARTI : PRSENCE REQUISE</p>
                    
                    <h2 className="event-date">SAMEDI<br/>8 NOVEMBRE</h2>
                    
                    <div className="event-divider">  </div>
                    
                    <p className="event-time">19H30</p>

                    <div className="event-stars-bottom">  </div>
                </section>

                {/* Warning banner */}
                {/* <div className="warning-banner">
                    <p className="warning-text">
                          ATTENTION CAMARADES : Les retardataires seront envoys au goulag  
                    </p>
                </div> */}

                {/* DRESS CODE SECTION */}
                <section className="dress-code-section">
                   {/*  <div className="dress-code-badge">DIRECTIVE VESTIMENTAIRE</div> */}
                    
                    <div className="dress-code-content">
                        <div className="dress-code-header">
                            <span className="dress-code-symbol"></span>
                            <h3 className="dress-code-title">TENUE CORRECTE EXIGE</h3>
                            <span className="dress-code-symbol"></span>
                        </div>
                        
                        <p className="dress-code-text">
                            Proltarienne et <span className="dress-code-highlight">sans frivolits bourgeoises</span>.
                        </p>
                    </div>
                </section>

                {/* Menu Notice */}
                <section className="menu-section">
                    <div className="menu-badge">CONFIDENTIEL</div>

                    <p className="menu-title">
                        Le menu sera rvl<br/>le jour du dner
                    </p>
                    
                    
                    <p className="menu-quote">
                        "La surprise fait partie du plan !"<br/>
                        <span className="menu-quote-author"> Lnine (probablement)</span>
                    </p>

                    {/* NEW DRINKS DIRECTIVE SECTION */}
                    <div className="drinks-directive">
                        <div className="drinks-content">
                            <div className="drinks-header">
                                <span className="drinks-symbol"></span>
                                <h3 className="drinks-title">CONTRIBUTION OBLIGATOIRE</h3>
                                <span className="drinks-symbol"></span>
                            </div>
                            
                            <p className="drinks-text">
                                Chaque camarade est invit  contribuer  la <span className="drinks-highlight">gloire collective</span> en 
                                apportant une boisson de son choix  alcoolise ou non.
                            </p>
                            
                            <div className="drinks-emphasis-box">
                                <p className="drinks-emphasis">
                                    Le Comit Central encourage le respect des traditions,<br/>
                                    la <strong>VODKA</strong> demeurant la boisson du peuple.
                                </p>
                            </div>
                            
                        </div>
                    </div>
                </section>
                {/* Footer propaganda */}
                <footer className="restaurant-footer">
                    <div className="footer-symbol"></div>
                    <div className="footer-badge">
                        <p className="footer-badge-text">  BIENTT,  ! </p>
                    </div>
                </footer>
            </div>
        </div>
    );
};

export default Restaurant;